import axios from 'axios';
const ORDER_API_BASE_URL="/api/getoprice";
class orderService{
    getprices(social,service,count){
        return axios.get(ORDER_API_BASE_URL+'/'+social+'?service='+service+'&count='+count);
    }

    getMinMax(){
        return axios.get(ORDER_API_BASE_URL);
    }
    
    saveorder(order){
        return axios.post(ORDER_API_BASE_URL,order);
      
    }
    
}
export default new orderService()