/* eslint-disable react/style-prop-object */
/* eslint-disable jsx-a11y/anchor-is-valid */
// eslint-disable-next-line no-unused-vars
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import validator from 'validator';
import passwordValidator from 'password-validator';
import toast from 'toast-me';

import userServices from '../services/userServices';
import SessionKeystore from 'session-keystore'

// import { data } from 'jquery';

class Register extends React.Component {


	constructor(props) {
		super(props)
		this.state = {

			email: '',
			key: '',
			key1: '',
			error: ''

		}
		// this.userSignup = this.userSignup.bind(this);
		this.changeEmailHandlter = this.changeEmailHandlter.bind(this);
		this.changekey = this.changekey.bind(this);
		// Create a schema
		this.checkuser();
	}

	checkuser=()=>{
		const store = new SessionKeystore({ name: 'webpromo' });
		const key = store.get('user');
		if (key != null) {
			this.props.history.push('/login');
		}
	}

	userSignup = (e) => {
		var schema = new passwordValidator();

		// Add properties to it
		schema
			.is().min(8)                                    // Minimum length 8
			.is().max(30)                                  // Maximum length 30
			.has().uppercase()                              // Must have uppercase letters
			.has().lowercase()                              // Must have lowercase letters
			.has().digits(1)                                // Must have at least 1 digits
			.has().not().spaces();                           // Should not have spaces
		// .is().not().oneOf(['Passw0rd', 'Password123']); // Blacklist these values

		var key = document.getElementById("key").value;
		var key1 = document.getElementById("key1").value;
		var email = document.getElementById("email").value;

		if (email == "") {
			// this.setState({
			// 	error: "Please enter your password"
			// });
			toast('Please enter your email');
		} else if (key == "") {
			// this.setState({
			// 	error: "Please enter your email"
			// });
			toast('Please enter your password');
		} else if (key1 == "") {
			// this.setState({
			// 	error: "Please enter your confirm password"
			// });
			toast('Please enter your confirm password');
		} else if (key !== key1) {
			// this.setState({
			// 	error: "Passwords did not match!"
			// });
			toast('Passwords did not match!');
		} else if (!(validator.isEmail(email))) {
			// this.setState({
			// 	error: "Invalid email!"
			// });
			toast('Passwords did not match!');
		} else if (!schema.validate(key)) {
			var errors = schema.validate(key, { list: true });
			var final_error = "";
			if (errors["0"] === 'min') {
				final_error = "Password must have minimum 8 characters";
			} else if (errors["0"] === 'max') {
				final_error = "Password must have maximum 30 characters";
			} else if (errors["0"] === 'uppercase') {
				final_error = "Password must have at least 1 upper case";
			} else if (errors["0"] === 'lowecase') {
				final_error = "Password must have at least 1 lower case";
			} else if (errors["0"] === 'digits') {
				final_error = "Password must have at least 1 digit";
			} else if (errors["0"] === 'spaces') {
				final_error = "Password must not have spaces";
			}
			// this.setState({
			// 	error: final_error
			// });
			toast(final_error);
		}
		else {
			e.preventDefault();
			let user = { email: this.state.email, key: this.state.key, key1: this.state.key1 }
			console.log('user=>' + JSON.stringify(user));
			userServices.createUser(user).then(res => {
				// this.props.history.push('/Login');

				if (res.status === 200 && res.statusText === 'OK') {
					if (res.data === 'Success') {
						// this.setState({
						// 	error: "Success"
						// });
						this.props.history.push('/login');
					} else if (res.data === 'Already exist') {
						// this.setState({
						// 	error: "Email is already existed"

						// });
						toast("Email is already existed");
					} else {
						this.setState({
							error: res.data
						});
					}
				}

			});
		}
	}

	changeEmailHandlter = (event) => {
		this.setState({
			email: event.target.value
		});
	}
	changekey = (event) => {
		this.setState({
			key: event.target.value
		});
	}

	changekey1 = (event) => {
		this.setState({
			key1: event.target.value
		});
	}


	render() {
		return (

			<div>

				<div class="limiter">
					<div class="container-login100">
						<div class="wrap-login100">



							<div class="login100-form validate-form">
								<span class="login100-form-title">
									Register
					</span>
								<h3 id="alert" style={{ color: "red", margin: "center", fontSize: "20px", marginLeft: "25%", marginRight: "20%", marginBottom: "10px" }}>{this.state.error}</h3>

								<div class="regwithemal1">
									Register With Email
					</div>

								<div class="wrap-input100 validate-input" data-validate="Valid email is required: ex@abc.xyz">
									<input class="input100" type="text" name="email" id="email" placeholder="Email" value={this.state.email} onChange={this.changeEmailHandlter} />
									<span class="focus-input100"></span>
									<span class="symbol-input100">
										<i class="fa fa-envelope" aria-hidden="true"></i>
									</span>
								</div>
								<br></br>

								<div class="wrap-input100 validate-input" data-validate="Password is required">
									<input class="input100" type="password" name="pass" id="key" placeholder="Password" value={this.state.key} onChange={this.changekey} />
									<span class="focus-input100"></span>
									<span class="symbol-input100">
										<i class="fa fa-lock" aria-hidden="true"></i>
									</span>
								</div>

								<br></br>

								<div class="wrap-input100 validate-input" data-validate="Password is required">
									<input class="input100" type="password" name="pass" id="key1" placeholder=" Confirm password" value={this.state.key1} onChange={this.changekey1} />
									<span class="focus-input100"></span>
									<span class="symbol-input100">
										<i class="fa fa-lock" aria-hidden="true"></i>
									</span>
								</div>

								<br></br>
								<div class="container-login100-form-btn">
									<button class="login100-form-btn" onClick={this.userSignup}>
										SIGN UP
						</button>
								</div>


								<br></br>

								{/* <!--line --> */}
								<div class="container">

									<hr class="hr-text" data-content="AND" />


								</div>

								<div class="regwithemal1">
									Register With Another Service
									</div>


								<div class="facebooklog">
									<img src="assets\images\facebook.png" style={{ width: "80%", height: "40% " }} alt=""></img>
								</div>

								<div class="facebooklog">
									<img src="assets\images\googlesignup.png" style={{ width: "80%", height: "40% " }} alt=""></img>
								</div>

								<div class="text-center p-t-12">
									<span class="txt1">
										Forgot Username Or Password?
						</span>&nbsp;

									<a class="txt2" href="#">
										Reset Password
						</a>
								</div>

								<div class="text-center p-t-12">
									<span class="txt1">
										Already Have An Account ?&nbsp;&nbsp;
						</span>
									<Link to="/login" >
										<a class="txt2" href="#">
											Log in
						</a>
									</Link>

								</div>

							</div>
						</div>
					</div>
				</div>

			</div>

		)

	}

}
export default Register